<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row type="flex" justify="space-between">
        <el-col :span="6">
          <el-input placeholder="查找用户" v-model="queryInfo.query">
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="3">
          <el-button type="primary" @click="addDialogVisible = true"
            >添加用户</el-button
          >
        </el-col>
      </el-row>
      <el-table :data="userList">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="username" label="用户名"></el-table-column>
        <el-table-column prop="role_name" label="角色"></el-table-column>
        <el-table-column prop="email" label="邮箱"></el-table-column>
        <el-table-column prop="mobile" label="手机号"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="openEditDialog(scope.row.id)"
              >修改</el-button
            >
            <el-button
              type="warning"
              icon="el-icon-setting"
              @click="openSetDialog(scope.row.id)"
              size="mini"
              >设置</el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delete"
              @click="openDeleteDialog(scope.row.id)"
              size="mini"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="queryInfo.pagenum"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <div>
      <!-- 添加用户 -->
      <el-dialog
        title="添加用户"
        :visible.sync="addDialogVisible"
        @close="resetAddForm"
      >
        <el-form
          :model="addUserInfo"
          :rules="addRule"
          ref="addForm"
          label-width="80px"
        >
          <el-form-item label="用户名" prop="username">
            <el-input v-model="addUserInfo.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="addUserInfo.password"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="addUserInfo.email"></el-input>
          </el-form-item>
          <el-form-item label="手机号" prop="mobile">
            <el-input v-model="addUserInfo.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addUser">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 修改用户信息 -->
      <el-dialog
        title="修改用户信息"
        :visible.sync="editDialogVisible"
        @close="resetEditForm"
      >
        <el-form
          :model="userInfo"
          :rules="editRule"
          label-width="80px"
          ref="editForm"
        >
          <el-form-item label="用户名">
            <el-input :value="userInfo.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="userInfo.email"></el-input>
          </el-form-item>
          <el-form-item label="手机号" prop="mobile">
            <el-input v-model="userInfo.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="editUser">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 设置用户角色 -->
      <el-dialog title="设置用户角色" :visible.sync="setDialogVisible">
        <el-form label-width="80px">
          <el-form-item label="用户名">
            <el-input :value="userInfo.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="选择角色">
            <el-select v-model="roleId" placeholder="请选择">
              <el-option
                v-for="item in roleList"
                :key="item.id"
                :label="item.roleName"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="setRole">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 获取用户列表
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      userList: [],
      total: 0,

      // 获取角色列表
      roleList: [],

      // 添加用户
      addDialogVisible: false,
      addRule: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        // email: [{ required: true, message: "请输入邮箱", trigger: "blur" }],
        // mobile: [{ required: true, message: "请输入手机号", trigger: "blur" }],
      },
      addUserInfo: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },

      // 查询用户信息
      userInfo: {},

      // 修改用户信息
      editDialogVisible: false,
      editRule: {
        email: [{ required: true, message: "请输入邮箱", trigger: "blur" }],
        mobile: [{ required: true, message: "请输入手机号", trigger: "blur" }],
      },

      // 设置用户角色
      setDialogVisible: false,
      roleId: null,
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    // 获取用户列表
    async getUserList() {
      const { data: res } = await this.$http.get("users", {
        params: this.queryInfo,
      });
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("获取失败");
      } else {
        this.userList = res.data.users;
        this.total = res.data.total;
      }
    },

    // 获取角色列表
    async getRoleList() {
      const { data: res } = await this.$http.get("roles");
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("获取失败");
      } else {
        this.roleList = res.data;
      }
    },

    // 条件查找用户
    search() {
      this.queryInfo.pagenum = 1;
      this.getUserList();
    },

    // 添加用户
    resetAddForm() {
      this.$refs.addForm.resetFields();
    },
    addUser() {
      this.$refs.addForm.validate(async (valid) => {
        if (!valid) {
          return;
        } else {
          const { data: res } = await this.$http.post(
            "users",
            this.addUserInfo
          );
          // console.log(res);
          if (res.meta.status !== 201) {
            this.$message.error("添加失败");
          } else {
            this.$message.success("添加成功");
            this.addDialogVisible = false;
            this.getUserList();
          }
        }
      });
    },

    // 查询用户信息
    async queryUser(id) {
      const { data: res } = await this.$http.get(`users/${id}`);
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("查询失败");
      } else {
        this.userInfo = res.data;
      }
    },

    // 修改用户信息
    openEditDialog(id) {
      this.queryUser(id);
      this.editDialogVisible = true;
    },
    resetEditForm() {
      this.$refs.editForm.resetFields();
    },
    editUser() {
      this.$refs.editForm.validate(async (valid) => {
        if (!valid) {
          return;
        } else {
          const { data: res } = await this.$http.put(
            `users/${this.userInfo.id}`,
            this.userInfo
          );
          // console.log(res);
          if (res.meta.status !== 200) {
            this.$message.error("修改失败");
          } else {
            this.$message.success("修改成功");
            this.editDialogVisible = false;
            this.getUserList();
          }
        }
      });
    },

    // 设置用户角色
    openSetDialog(id) {
      this.queryUser(id);
      this.getRoleList();
      this.setDialogVisible = true;
    },
    async setRole() {
      if (!this.roleId) {
        this.$message.error("请选择设置角色");
      } else {
        const { data: res } = await this.$http.put(
          `users/${this.userInfo.id}/role`,
          {
            rid: this.roleId,
          }
        );
        // console.log(res);
        if (res.meta.status !== 200) {
          this.$message.error("设置失败");
        } else {
          this.$message.success("设置成功");
          this.setDialogVisible = false;
          this.getUserList();
        }
      }
    },

    // 删除用户
    openDeleteDialog(id) {
      // console.log(id);
      this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.removeUser(id);
        })
        .catch(() => {
          this.$message.info("已取消删除");
        });
    },
    async removeUser(id) {
      const { data: res } = await this.$http.delete(`users/${id}`);
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("删除失败");
      } else {
        this.$message.success("删除成功");
        this.getUserList();
      }
    },

    // 分页处理函数
    handleCurrentChange() {
      this.getUserList();
    },
    handleSizeChange(val) {
      this.queryInfo.pagesize = val;
      this.queryInfo.pagenum = 1;
      this.getUserList();
    },
  },
};
</script>

