<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!-- <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="请输入内容" v-model="queryInfo.query">
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getOrderList"
            ></el-button>
          </el-input>
        </el-col>
      </el-row> -->
      <el-table :data="orderList">
        <el-table-column type="index" label="#"></el-table-column>
        <!-- <el-table-column prop="order_id" label="id"></el-table-column> -->
        <el-table-column prop="order_number" label="订单编号"></el-table-column>
        <el-table-column label="金额" width="100px">
          <template slot-scope="scope">
            <span>&yen;{{ scope.row.order_price }}</span>
          </template>
        </el-table-column>
        <el-table-column label="更新时间">
          <template>
            <span>{{ date | dateFormat }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-truck"
              @click="openDiglog(scope.row.id)"
              size="mini"
              >查看物流</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="queryInfo.pagenum"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <el-dialog title="查看物流信息" :visible.sync="dialogVisible">
      <el-timeline>
        <el-timeline-item
          v-for="(item, index) in transInfo"
          :key="index"
          :timestamp="item.time"
        >
          {{ item.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
export default {
  filters: {
    dateFormat(val) {
      const dt = val;
      const y = dt.getFullYear();
      const m = (dt.getMonth() + 1 + "").padStart(2, "0");
      const d = (dt.getDate() + "").padStart(2, "0");
      const hh = (dt.getHours() + "").padStart(2, "0");
      const mm = (dt.getMinutes() + "").padStart(2, "0");
      const ss = (dt.getSeconds() + "").padStart(2, "0");
      return `${y}-${m}-${d} ${hh}:${mm}:${ss}`;
    },
  },
  data() {
    return {
      date: null,
      // 获取订单列表
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      orderList: [],
      total: 0,

      // 查看物流信息
      dialogVisible: false,
      transInfo: [],
    };
  },
  created() {
    this.date = new Date();
    this.getOrderList();
  },
  methods: {
    // 获取订单列表
    async getOrderList() {
      const { data: res } = await this.$http.get("orders", {
        params: this.queryInfo,
      });
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("获取失败");
      } else {
        this.orderList = res.data.goods;
        this.total = res.data.total;
      }
    },

    // 分页处理函数
    handleSizeChange(val) {
      this.queryInfo.pagesize = val;
      this.queryInfo.pagenum = 1;
      this.getOrderList();
    },
    handleCurrentChange() {
      this.getOrderList();
    },

    // 查看物流信息
    openDiglog(id) {
      this.querytransportation(id);
      this.dialogVisible = true;
    },
    async querytransportation(id) {
      const { data: res } = await this.$http.get(`kuaidi/${id}`);
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("查询失败");
      } else {
        this.transInfo = res.data;
      }
    },
  },
};
</script>

