<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品参数</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-cascader
        v-model="value"
        :options="cateList"
        :props="props"
        @change="getParams"
        placeholder="请选择商品分类"
      >
      </el-cascader>
      <el-tabs v-model="activeName" @tab-click="getParams" stretch>
        <el-tab-pane label="固定参数" name="only">
          <el-table :data="params">
            <el-table-column
              prop="attr_name"
              label="参数名称"
              width="300px"
            ></el-table-column>
            <el-table-column prop="attr_vals" label="参数值"></el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="变化参数" name="many">
          <el-table :data="params">
            <el-table-column
              prop="attr_name"
              label="参数名称"
              width="300px"
            ></el-table-column>
            <el-table-column prop="attr_vals" label="参数值"></el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 获取分类列表
      cateList: [],
      props: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },

      // 获取当前参数
      select: [],
      activeName: "only",
      params: [],
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    // 获取分类列表
    async getCateList() {
      const { data: res } = await this.$http.get("categories");
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("获取失败");
      } else {
        this.cateList = res.data;
      }
    },

    // 获取当前参数
    async getParams() {
      const { data: res } = await this.$http.get(
        `categories/${this.value[2]}/attributes`,
        {
          params: { sel: this.activeName },
        }
      );
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("获取失败");
      } else {
        this.params = res.data;
        console.log(this.params);
      }
    },
  },
};
</script>

