<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert
        center
        :closable="false"
        title="请输入商品信息"
        type="info"
        show-icon
      >
      </el-alert>
      <el-form
        :model="addGoodsInfo"
        :rules="addRule"
        ref="addForm"
        label-width="80px"
      >
        <el-tabs tab-position="left" value="info">
          <el-tab-pane label="基本信息" name="info">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addGoodsInfo.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model.number="addGoodsInfo.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model.number="addGoodsInfo.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model.number="addGoodsInfo.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                v-model="addGoodsInfo.goods_cat"
                :options="cateList"
                :props="props"
              >
              </el-cascader>
            </el-form-item>
            <el-form-item label="商品介绍" prop="goods_introduce">
              <el-input v-model="addGoodsInfo.goods_introduce"> </el-input>
            </el-form-item>
            <el-button type="primary" @click="addGoods">添加商品</el-button>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="img"
            ><el-upload
              :action="uploadUrl"
              :headers="headers"
              :on-success="handleSuccess"
              :on-remove="handleRemove"
              list-type="picture"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload></el-tab-pane
          >
        </el-tabs>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 获取分类列表
      cateList: [],
      props: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },

      // 添加商品
      addGoodsInfo: {
        goods_name: "",
        goods_price: null,
        goods_number: null,
        goods_weight: null,
        goods_cat: [],
        goods_introduce: "",
        pics: [],
      },
      addRule: {
        goods_name: [
          { required: true, message: "请输入商品名称", trigger: "blur" },
        ],
        goods_price: [
          {
            type: "number",
            required: true,
            message: "请输入正确价格",
            trigger: "blur",
          },
        ],
        goods_number: [
          {
            type: "number",
            required: true,
            message: "请输入正确数量",
            trigger: "blur",
          },
        ],
        goods_weight: [
          {
            type: "number",
            required: true,
            message: "请输入正确重量",
            trigger: "blur",
          },
        ],
        goods_cat: [{ required: true, message: "请选择分类", trigger: "blur" }],
      },

      // 上传图片
      uploadUrl: "https://lianghj.top:8888/api/private/v1/upload",
      headers: {
        Authorization: window.sessionStorage.getItem("token"),
      },
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    // 获取分类列表
    async getCateList() {
      const { data: res } = await this.$http.get("categories");
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("获取失败");
      } else {
        this.cateList = res.data;
      }
    },

    // 添加商品
    addGoods() {
      this.$refs.addForm.validate(async (valid) => {
        if (!valid) {
          return;
        } else {
          if (Array.isArray(this.addGoodsInfo.goods_cat)) {
            this.addGoodsInfo.goods_cat = this.addGoodsInfo.goods_cat.join(",");
          }
          const { data: res } = await this.$http.post(
            "goods",
            this.addGoodsInfo
          );
          // console.log(res);
          if (res.meta.status !== 201) {
            this.$message.error("添加失败");
          } else {
            this.$message.success("添加成功");
            // this.$refs.addForm.resetFields();
          }
        }
      });
    },

    // 上传图片
    handleSuccess(res) {
      // const info = { pic: res.data.tmp_path };
      // this.addGoodsInfo.pics.push(info);
    },
    handleRemove(file) {
      // const path = file.response.data.tmp_path;
      // const index = this.addGoodsInfo.pics.findIndex((item) => {
      //   item.pcis === path;
      // });
      // this.addGoodsInfo.pics.splice(index);
    },
  },
};
</script>

<style lang="less" scoped>
.el-form {
  margin: 30px 0;
}
</style>