<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row type="flex" justify="end">
        <el-col :span="3">
          <el-button type="primary" @click="addDialogVisible = true"
            >添加角色</el-button
          >
        </el-col>
      </el-row>
      <el-table :data="rolesList">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="roleName" label="角色名称"></el-table-column>
        <el-table-column prop="roleDesc" label="相关描述"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              @click="openEditDialog(scope.row.id)"
              size="mini"
              >编辑</el-button
            >
            <el-button
              type="warning"
              icon="el-icon-setting"
              @click="openSetDialog(scope.row)"
              size="mini"
              >设置</el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delete"
              @click="openDeleteDialog(scope.row.id)"
              size="mini"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <div>
      <!-- 添加角色 -->
      <el-dialog
        title="添加角色"
        :visible.sync="addDialogVisible"
        @close="resetAddForm"
      >
        <el-form
          :model="addRoleInfo"
          :rules="addRule"
          ref="addForm"
          label-width="80px"
        >
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="addRoleInfo.roleName"></el-input>
          </el-form-item>
          <el-form-item label="相关描述" prop="roleDesc">
            <el-input v-model="addRoleInfo.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addRole">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 修改角色信息 -->
      <el-dialog
        title="修改角色信息"
        :visible.sync="editDialogVisible"
        @close="resetEditForm"
      >
        <el-form
          :model="roleInfo"
          :rules="editRule"
          ref="editForm"
          label-width="80px"
        >
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="roleInfo.roleName"></el-input>
          </el-form-item>
          <el-form-item label="相关描述">
            <el-input v-model="roleInfo.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="editRole">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 设置角色权限 -->
      <el-dialog
        title="设置角色权限"
        :visible.sync="setDialogVisible"
        @close="nodeKey = []"
      >
        <el-tree
          :data="rightList"
          :props="defaultProps"
          show-checkbox
          default-expand-all
          node-key="id"
          :default-checked-keys="nodeKey"
          ref="tree"
        ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="setRights">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 获取角色列表
      rolesList: [],

      // 获取角色列表
      rightList: [],

      // 添加角色
      addDialogVisible: false,
      addRule: {
        roleName: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
      },
      addRoleInfo: {
        roleName: "",
        roleDesc: "",
      },

      // 查询角色信息
      roleInfo: {},

      // 修改角色信息
      editDialogVisible: false,
      editRule: {
        roleName: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
      },

      // 设置角色权限
      setDialogVisible: false,
      defaultProps: {
        children: "children",
        label: "authName",
      },
      nodeKey: [],
    };
  },
  created() {
    this.getRoleList();
  },
  methods: {
    // 获取角色列表
    async getRoleList() {
      const { data: res } = await this.$http.get("roles");
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("获取失败");
      } else {
        this.rolesList = res.data;
      }
    },

    // 获取权限列表
    async getRightList() {
      const { data: res } = await this.$http.get("rights/tree");
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("获取失败");
      } else {
        this.rightList = res.data;
      }
    },

    // 添加角色
    resetAddForm() {
      this.$refs.addForm.resetFields();
    },
    addRole() {
      this.$refs.addForm.validate(async (valid) => {
        if (!valid) {
          return;
        } else {
          const { data: res } = await this.$http.post(
            "roles",
            this.addRoleInfo
          );
          // console.log(res);
          if (res.meta.status !== 201) {
            this.$message.error("添加失败");
          } else {
            this.$message.success("添加成功");
            this.addDialogVisible = false;
            this.getRoleList();
          }
        }
      });
    },

    // 查询角色信息
    async queryRole(id) {
      const { data: res } = await this.$http.get(`roles/${id}`);
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("查询失败");
      } else {
        this.roleInfo = res.data;
      }
    },

    // 修改角色信息
    resetEditForm() {
      this.$refs.editForm.resetFields();
    },
    openEditDialog(id) {
      this.queryRole(id);
      this.editDialogVisible = true;
    },
    editRole() {
      this.$refs.editForm.validate(async (valid) => {
        if (!valid) {
          return;
        } else {
          const { data: res } = await this.$http.put(
            `roles/${this.roleInfo.roleId}`,
            this.roleInfo
          );
          // console.log(res);
          if (res.meta.status !== 200) {
            this.$message.error("修改失败");
          } else {
            this.$message.success("修改成功");
            this.editDialogVisible = false;
            this.getRoleList();
          }
        }
      });
    },

    // 删除角色
    openDeleteDialog(id) {
      this.$confirm("此操作将永久删除该角色, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.removeRole(id);
        })
        .catch(() => {
          this.$message.info("已取消删除");
        });
    },
    async removeRole(id) {
      const { data: res } = await this.$http.delete(`roles/${id}`);
      if (res.meta.status !== 200) {
        this.$message.error("删除失败");
      } else {
        this.$message.success("删除成功");
        this.getRoleList();
      }
    },

    // 设置角色权限
    openSetDialog(role) {
      this.queryRole(role.id);
      this.getRightList();
      this.getNodeKey(role, this.nodeKey);
      this.setDialogVisible = true;
    },
    getNodeKey(node, arr) {
      if (!node.children) {
        return arr.push(node.id);
      }
      node.children.forEach((item) => {
        this.getNodeKey(item, arr);
      });
    },
    async setRights() {
      const keys = [
        ...this.$refs.tree.getCheckedKeys(),
        ...this.$refs.tree.getHalfCheckedKeys(),
      ];
      const rids = keys.join();
      const { data: res } = await this.$http.post(
        `roles/${this.roleInfo.roleId}/rights`,
        {
          rids,
        }
      );
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("设置失败");
      } else {
        this.$message.success("设置成功");
        this.setDialogVisible = false;
        this.getRoleList();
      }
    },
  },
};
</script>

