<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row type="flex" justify="space-between">
        <el-col :span="6">
          <el-input placeholder="查找商品" v-model="queryInfo.query">
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="3">
          <el-button type="primary" @click="handleClick">添加商品</el-button>
        </el-col>
      </el-row>
      <el-table :data="goodsList">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="goods_name" label="商品名称"></el-table-column>
        <el-table-column label="价格" width="100px">
          <template slot-scope="scope">
            <span>&yen;{{ scope.row.goods_price }}</span>
          </template>
        </el-table-column>
        <el-table-column label="更新时间" width="200px">
          <template>
            <span>{{ date | dateFormat }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="openEditDialog(scope.row.goods_id)"
              >修改</el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delete"
              @click="openDeleteDialog(scope.row.goods_id)"
              size="mini"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="queryInfo.pagenum"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 修改商品信息 -->
    <el-dialog
      title="修改商品信息"
      :visible.sync="editDialogVisible"
      @close="resetEditForm"
    >
      <el-form
        :model="goodsInfo"
        :rules="editRule"
        label-width="80px"
        ref="editForm"
      >
        <el-form-item label="商品名" prop="goods_name">
          <el-input v-model="goodsInfo.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="价格" prop="goods_price">
          <el-input v-model="goodsInfo.goods_price"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editGoods">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  filters: {
    dateFormat(val) {
      const dt = val;
      const y = dt.getFullYear();
      const m = (dt.getMonth() + 1 + "").padStart(2, "0");
      const d = (dt.getDate() + "").padStart(2, "0");
      const hh = (dt.getHours() + "").padStart(2, "0");
      const mm = (dt.getMinutes() + "").padStart(2, "0");
      const ss = (dt.getSeconds() + "").padStart(2, "0");
      return `${y}-${m}-${d} ${hh}:${mm}:${ss}`;
    },
  },
  data() {
    return {
      date: null,
      // 获取商品列表
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      goodsList: [],
      total: 0,

      // 查询商品信息
      goodsInfo: {},

      // 修改商品信息
      editDialogVisible: false,
      editRule: {
        goods_name: [
          { required: true, message: "请输入商品名", trigger: "blur" },
        ],
        goods_price: [
          { required: true, message: "请输入价格", trigger: "blur" },
        ],
      },
    };
  },
  created() {
    this.date = new Date();
    this.getGoodsList();
  },
  methods: {
    // 获取商品列表
    async getGoodsList() {
      const { data: res } = await this.$http.get("goods", {
        params: this.queryInfo,
      });
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("获取失败");
      } else {
        this.goodsList = res.data.goods;
        this.total = res.data.total;
      }
    },

    // 跳转添加商品
    handleClick() {
      this.$router.push("/home/goods/add");
    },

    // 条件查找商品
    search() {
      this.queryInfo.pagenum = 1;
      this.getGoodsList();
    },

    // 查询商品信息
    async queryGoods(id) {
      const { data: res } = await this.$http.get(`goods/${id}`);
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("查询失败");
      } else {
        this.goodsInfo = res.data;
      }
    },

    // 修改商品信息
    openEditDialog(id) {
      this.queryGoods(id);
      this.editDialogVisible = true;
    },
    resetEditForm() {
      this.$refs.editForm.resetFields();
    },
    editGoods() {
      this.$refs.editForm.validate(async (valid) => {
        if (!valid) {
          return;
        } else {
          const { data: res } = await this.$http.put(
            `goods/${this.goodsInfo.goods_id}`,
            this.goodsInfo
          );
          // console.log(res);
          if (res.meta.status !== 200) {
            this.$message.error("修改失败");
          } else {
            this.$message.success("修改成功");
            this.editDialogVisible = false;
            this.getGoodsList();
          }
        }
      });
    },

    // 删除商品
    openDeleteDialog(id) {
      // console.log(id);
      this.$confirm("此操作将永久删除该商品, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.removeGoods(id);
        })
        .catch(() => {
          this.$message.info("已取消删除");
        });
    },
    async removeGoods(id) {
      const { data: res } = await this.$http.delete(`goods/${id}`);
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("删除失败");
      } else {
        this.$message.success("删除成功");
        this.getGoodsList();
      }
    },

    // 分页处理函数
    handleSizeChange(val) {
      this.queryInfo.pagesize = val;
      this.queryInfo.pagenum = 1;
      this.getGoodsList();
    },
    handleCurrentChange() {
      this.getGoodsList();
    },
  },
};
</script>

